$sand: #C5C6C7;
$navy: #1F2833;
$void: #0B0C10;
$striking: #66FCF1;
$pure: #45A29E;



.HomePage {

    margin-top: 10rem;

    &__title {

        font-size: 1.7rem;
        margin-top: 6rem;
        text-align: center;
        color: $sand;

        @media only screen and(max-width: 410px) {
            font-size: 1.4rem;
        }
        @media only screen and(max-width: 315px) {
            font-size: 1.2rem;
        }


        

        .dev {
            color: $pure;
        }
    }

    &__section {
        padding: 2rem;
        
        margin-bottom: 2rem;

        &__about {
            display: flex;
            flex-flow: row wrap;
            justify-content: center;
            
               
            

            &__img-container {
                width: 200px;
                height: 200px;
                border-radius: 50%;
                
                background-image: url(./avatar.jpg);
                background-position: center;
                background-repeat: no-repeat;
                
                overflow: hidden;
                margin-top: 6rem;
                margin-right: 3rem;
                box-shadow: 0 1rem 1.5rem rgba($pure, 0.15);

                @media only screen and(max-width:800px) {
                    margin-right: 0rem;
                }
                

            }

            &__desc {
                width: 30rem;
                
                margin-top: 5rem;
                


                &__text {

                    font-size: 1.3rem;
                    margin-bottom: 2rem;

                    @media only screen and(max-width: 410px) {
                        font-size: 1.1rem;
                    }
                   
                    color: $sand;

                    .strong {
                        font-weight: 700;
                        color: $pure;
                    }
                }
            }
        }
    }

    .contact {

       width: 100%;
       
      
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;

        &__title {

            min-width: 5rem;
            color: $sand;
            margin-bottom: 1rem;
        }

        &__container {
            display: flex;
            flex-flow: row wrap;
            justify-content: space-around;
            min-width: 20rem;

            &__link {
             fill: $sand;

             &:hover {
                 fill: $pure;
             }

            }

            &__email {
                color: $pure;

            }
        }
    }

    .footer {
        width: 100%;
        padding: 2rem;
        display: flex;
        flex-flow: row wrap;
        &__copy {
            display: block;
            margin: auto;
            width: auto;
            font-size: 0.8rem;
            color: grey;

        }

    }
}